.wpe-like-count {
	line-height: 1;
}

.wpe-liked {
	opacity: 1;
	visibility: visible;
}

.wpe-like-count[hidden] {
	opacity: 0;
	visibility: hidden;
}

.wpe-like-count .wpe-icon--heart {
	height: auto;
	transition: opacity 0.2s;
	width: 18px;
}

.wpe-like-count .wpe-icon--heart {
	fill: currentColor;
	opacity: 0.6;
	stroke: transparent;
	stroke-width: 0;
}

.wpe-like-count__link {
	align-items: center;
	column-gap: 0.4em;
	display: inline-flex;
	filter: grayscale(1);
	line-height: 1;
	text-decoration: none;
	transition: filter 0.2s;
}

.wpe-like-count__link:hover {
	filter: none;
}

.wpe-like-count__link:hover .wpe-icon--heart {
	opacity: 1;
}

.wpe-like-count .avatar {
	border: 2px solid var(--wp--preset--color--contrast-3);
	border-radius: 50%;
	box-sizing: border-box;
	margin-right: 0.2em;
}
